import styles from './styles/sideMenu.module.css'

function SideMenu ({menuClick, changeMenuClicked}) {
    return (
        <nav className={`${menuClick ? "fixed bg-gray-900 bg-opacity-75": "hidden"} z-20 w-full h-full`}
            onClick={changeMenuClicked}
        >
            <div  className={`${menuClick ? "transform translate-x-0 transition-all ease-out duration-500": "transform -translate-x-full transition-all ease-out duration-500 "} flex w-11/12 z-20 h-full bg-white`}>
                {console.log("menu : ", menuClick ? "clicked ": "not clicked ")}
                <h1>Side Menu</h1>
            </div>

        </nav>
    )
}

export default SideMenu